import { DeckUtils } from 'engine/common'
import _ from 'lodash'
import { Blackjack } from './blackjack'
import {
  createNewHand,
  getActualCardsValue,
  getPossibleCardsValue,
} from './utility'

export const dealAction = (old: Blackjack): Blackjack => {
  const state = _.cloneDeep(old)

  const draw = DeckUtils.drawCards(state.deck, 1)

  if (state.dealer.cards.length < 2) {
    state.deck = draw.deck
    state.dealer.cards = [...state.dealer.cards, ...draw.cards]
    return state
  }

  state.hands.every((currentHand, idx) => {
    if (currentHand.cards.length === 2) return true

    const newCards = [...currentHand.cards, ...draw.cards]
    const cardsValue = getActualCardsValue(newCards)
    state.hands[idx] = {
      ...currentHand,
      cards: newCards,
      state: cardsValue === 21 ? 'standing' : currentHand.state,
    }

    return false
  })

  state.deck = draw.deck
  return state
}

export const prepareForBetsAction = (state: Blackjack): Blackjack => ({
  deck: {
    ...state.deck,
    graveyard: [
      ...state.deck.graveyard,
      ...state.hands.flatMap((hand) => hand.cards),
      ...state.dealer.cards,
    ],
  },
  hands: state.hands
    .filter((hand) => hand.type === 'main')
    .map(() => createNewHand(0)),
  dealer: { cards: [], state: 'waiting' },
  state: 'betting',
})

export const playDealerAction = (old: Blackjack): Blackjack => {
  const state = _.cloneDeep(old)
  state.dealer.state = 'playing'
  const cardsValue = getPossibleCardsValue(state.dealer.cards)

  if (cardsValue.every((value) => value > 21)) {
    state.dealer.state = 'bust'
    return state
  }
  if (cardsValue.some((value) => value >= 17)) {
    state.dealer.state = 'standing'
    return state
  }

  const draw = DeckUtils.drawCards(state.deck, 1)
  state.dealer.state = 'waiting'
  state.deck = draw.deck
  state.dealer.cards.push(draw.cards[0])
  return state
}
