import { Hand, Dealer } from './blackjack'
import { getActualCardsValue } from './utility'

export const StateTriggers = {
  everyoneBet: (hands: Hand[]) => hands.every((hand) => hand.bet > 0),
  everyoneBeenDealt: (hands: Hand[], dealer: Dealer) =>
    hands.every((hand) => hand.cards.length === 2 && dealer.cards.length === 2),
  everyonePlayed: (hands: Hand[]) =>
    hands.every(
      (hand) =>
        hand.state !== 'playing' || getActualCardsValue(hand.cards) === 21,
    ),
  dealerPlayed: (dealer: Dealer) =>
    dealer.state === 'bust' || dealer.state === 'standing',
}
