import { createSlice } from '@reduxjs/toolkit'
import {
  betAction,
  Blackjack,
  createNewHand,
  dealAction,
  doubleDownAction,
  hitAction,
  playDealerAction,
  PossibleBlackjackStates,
  prepareForBetsAction,
  splitAction,
  standAction,
} from 'engine/blackjack'
import { DeckUtils } from 'engine/common'

const initialState: Blackjack = {
  state: 'betting',
  deck: DeckUtils.reshuffle(DeckUtils.markCards(DeckUtils.createNew())),
  dealer: { cards: [], state: 'waiting' },
  hands: [createNewHand(0)],
}

export const blackjackSlice = createSlice({
  name: 'blackjack',
  initialState,
  reducers: {
    bet: betAction,
    hit: hitAction,
    stand: standAction,
    doubleDown: doubleDownAction,
    split: splitAction,
    deal: dealAction,
    playDealer: playDealerAction,
    prepareForBets: prepareForBetsAction,
    changeGameState: (
      state,
      action: { payload: { state: PossibleBlackjackStates } },
    ) => ({ ...state, state: action.payload.state }),
  },
})

export const {
  bet,
  hit,
  stand,
  deal,
  doubleDown,
  playDealer,
  split,
  prepareForBets,
  changeGameState,
} = blackjackSlice.actions
export default blackjackSlice.reducer
