import { calculateWinnings, StateTriggers } from 'engine/blackjack'
import { wait } from 'utility'
import {
  bet,
  changeGameState,
  deal,
  playDealer,
  prepareForBets,
} from './blackjackSlice'
import { addMoney, removeMoney } from './playerSlice'
import { startAppListening } from './store'

const BlackjackListeners = {
  onAfterBet: () =>
    startAppListening({
      actionCreator: bet,
      effect: async (action, listenerApi) => {
        listenerApi.dispatch(removeMoney({ amount: action.payload.bet }))
        if (StateTriggers.everyoneBet(listenerApi.getState().blackjack.hands))
          listenerApi.dispatch(changeGameState({ state: 'dealing' }))
      },
    }),
  onDealNeeded: () =>
    startAppListening({
      predicate: (action, currentState) =>
        (action.type === 'blackjack/deal' ||
          action.type === 'blackjack/changeGameState') &&
        currentState.blackjack.state === 'dealing' &&
        !StateTriggers.everyoneBeenDealt(
          currentState.blackjack.hands,
          currentState.blackjack.dealer,
        ),
      effect: async (action, listenerApi) => {
        await wait(550).then(() => listenerApi.dispatch(deal()))
      },
    }),
  onEveryoneBeenDealt: () =>
    startAppListening({
      predicate: (action, currentState) =>
        action.type === 'blackjack/deal' &&
        currentState.blackjack.state === 'dealing' &&
        StateTriggers.everyoneBeenDealt(
          currentState.blackjack.hands,
          currentState.blackjack.dealer,
        ),
      effect: async (action, listenerApi) => {
        listenerApi.dispatch(changeGameState({ state: 'playerTurn' }))
      },
    }),
  onEveryonePlayed: () =>
    startAppListening({
      predicate: (action, currentState) =>
        currentState.blackjack.state === 'playerTurn' &&
        StateTriggers.everyonePlayed(currentState.blackjack.hands),
      effect: async (action, listenerApi) => {
        listenerApi.dispatch(changeGameState({ state: 'dealerTurn' }))
      },
    }),
  onDealersTurn: () =>
    startAppListening({
      predicate: (action, currentState) =>
        (action.type === 'blackjack/playDealer' ||
          action.type === 'blackjack/changeGameState') &&
        currentState.blackjack.state === 'dealerTurn' &&
        !StateTriggers.dealerPlayed(currentState.blackjack.dealer),
      effect: async (action, listenerApi) => {
        await wait(1000).then(() => listenerApi.dispatch(playDealer()))
      },
    }),
  onDealerPlayed: () =>
    startAppListening({
      predicate: (action, currentState) =>
        StateTriggers.dealerPlayed(currentState.blackjack.dealer) &&
        currentState.blackjack.state === 'dealerTurn',
      effect: async (action, listenerApi) => {
        listenerApi.dispatch(changeGameState({ state: 'payout' }))
        const win = listenerApi
          .getState()
          .blackjack.hands.reduce<number>(
            (sum, hand) =>
              sum +
              calculateWinnings(hand, listenerApi.getState().blackjack.dealer),
            0,
          )
        await wait(1000).then(() =>
          listenerApi.dispatch(addMoney({ amount: win })),
        )
        await wait(1000).then(() => listenerApi.dispatch(prepareForBets()))
      },
    }),
}

export const startBlackjackListeners = () =>
  Object.values(BlackjackListeners).forEach((listener) => listener())
